body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    sans-serif;
  background: #f5f5f7;
}

#root {
  height: 100vh;
}

/* AUTH */

.auth-page {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth-card {
  background: #ffffff;
  padding: 24px 28px;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  width: 320px;
}

.auth-card h2 {
  margin-top: 0;
  margin-bottom: 16px;
}

.auth-card form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.auth-card label {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  gap: 4px;
}

.auth-card input {
  padding: 8px 10px;
  border: 1px solid #cfd3dc;
  border-radius: 6px;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 8px 12px;
  background: #2563eb;
  color: white;
  font-size: 14px;
}

button:disabled {
  opacity: 0.6;
  cursor: default;
}

.link-button {
  background: none;
  color: #2563eb;
  padding: 0;
  border: none;
  font-size: 13px;
}

.error {
  color: #b91c1c;
  margin-top: 4px;
}

.error.small {
  font-size: 12px;
}

.info {
  color: #4b5563;
}

.info.small {
  font-size: 12px;
}

/* MAIN LAYOUT */

.main-layout {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 260px;
  background: #111827;
  color: #e5e7eb;
  padding: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.sidebar-inner {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.sidebar-header-mobile {
  display: none;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

/* Mobile */
.sidebar-toggle-mobile {
  display: none;
}

.sidebar h3 {
  margin: 0;
}

.sidebar-section h4 {
  margin: 8px 0;
}

.sidebar input[type="text"] {
  width: 90%;
  padding: 6px 8px;
  border-radius: 6px;
  border: 1px solid #4b5563;
  background: #111827;
  color: #e5e7eb;
}

@media (max-width: 768px) {
  .main-layout {
    display: block;
  }

  .sidebar {
    position: fixed;
    inset: 0;
    max-width: 280px;
    transform: translateX(-100%);
    transition: transform 0.2s ease-out;
    z-index: 50;
  }

  .sidebar-mobile-open {
    transform: translateX(0);
  }

  .sidebar-mobile-closed {
    transform: translateX(-100%);
  }

  .sidebar-header-mobile {
    display: flex;
  }

  .main-content {
    height: 100vh;
    padding: 12px;
  }

  .sidebar-toggle-mobile {
    display: inline-block;
    padding: 6px 10px;
    background: #111827;
    color: #e5e7eb;
  }
}

.tag-form {
  display: flex;
  gap: 8px;
}

.tag-form input {
  flex: 1;
}

.tag-form button {
  padding: 4px 8px;
  background: #10b981;
  font-size: 16px;
}

.tag-list {
  max-height: 40vh;
  overflow: auto;
  border-top: 1px solid #374151;
  padding-top: 8px;
}

.tag-item {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  margin-bottom: 4px;
  cursor: pointer;
}

.tag-item input {
  cursor: pointer;
}

.sidebar-section {
  border-top: 1px solid #374151;
  padding-top: 8px;
}

/* MAIN CONTENT */

.main-content {
  flex: 1;
  padding: 16px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.breadcrumbs {
  font-size: 14px;
}

.header-actions {
  display: flex;
  gap: 8px;
}

/* CARDS */

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 12px;
  align-items: stretch;
}

.card {
  background: #ffffff;
  border-radius: 10px;
  padding: 12px 14px;
  box-shadow: 0 4px 12px rgba(15, 23, 42, 0.06);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 6px;
  border: 1px solid transparent;
}

.card:hover {
  border-color: #2563eb;
}

.card-title {
  font-weight: 600;
}

.card-description {
  font-size: 13px;
  color: #4b5563;
}

.card-label {
  font-size: 11px;
  color: #6b7280;
  margin-top: auto;
  text-transform: uppercase;
}

/* MODALS */

.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 40;
}

.modal {
  background: #ffffff;
  padding: 16px 18px;
  border-radius: 12px;
  width: 400px;
  max-height: 90vh;
  overflow: auto;
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.3);
}

.modal.large {
  width: 640px;
}

.modal h3 {
  margin-top: 0;
}

.modal form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.modal label {
  display: flex;
  flex-direction: column;
  gap: 4px;
  font-size: 14px;
}

.modal input[type="text"],
.modal input[type="password"],
.modal textarea {
  padding: 6px 8px;
  border-radius: 6px;
  border: 1px solid #cbd5e1;
  font-size: 14px;
}

.modal textarea {
  resize: vertical;
}

.modal-actions {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}

.monospace {
  font-family: "JetBrains Mono", "Fira Code", monospace;
}

.tag-pills {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-top: 6px;
}

.tag-pill {
  font-size: 11px;
  padding: 2px 6px;
  border-radius: 999px;
  background: #e5e7eb;
  color: #374151;
}